"use client";
import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { supabase } from "@/lib/supabaseClient";

export default function AdminDashboard() {
  const router = useRouter();
  const [user, setUser] = useState<any>(null);

  useEffect(() => {
    const getUser = async () => {
      const { data, error } = await supabase.auth.getUser();

      if (error || !data.user) {
        router.push("/admin/login"); // redirect if not logged in
        return;
      }

      setUser(data.user);
    };

    getUser();
  }, [router]);

  const handleLogout = async () => {
    await supabase.auth.signOut();
    router.push("/admin/login");
  };

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col items-center justify-center">
      <h1 className="text-3xl font-bold mb-6">
        Welcome, {user?.email || "Loading..."}
      </h1>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 w-4/5">
        <div className="p-6 bg-white rounded-lg shadow">
          <h2 className="font-semibold text-xl mb-2">Upcoming NFO</h2>
          <p>List or manage upcoming NFOs here.</p>
        </div>
        <div className="p-6 bg-white rounded-lg shadow">
          <h2 className="font-semibold text-xl mb-2">Bonds</h2>
          <p>Manage bonds listed on your site.</p>
        </div>
        <div className="p-6 bg-white rounded-lg shadow">
          <h2 className="font-semibold text-xl mb-2">Clients</h2>
          <p>Manage or view client submissions.</p>
        </div>
      </div>

      <button
        onClick={handleLogout}
        className="mt-8 bg-red-500 text-white px-4 py-2 rounded hover:bg-red-600 transition"
      >
        Logout
      </button>
    </div>
  );
}
